import { useState, useEffect } from "react";
import {
  Card,
  CardHeader,
  Row,
  Col,
  InputGroup,
  InputGroupText,
  Input,
  Label,
  Button,
  Badge,
  Spinner,
} from "reactstrap";
import { Icon } from "@iconify/react";
import UserAvatar from "../components/UserAvatar";
import DataTable from "react-data-table-component";
import DataTablePagination from "../components/DataTablePagination";
import { findUpper } from "../services/Utils";
import Http from "../services/Http";
import moment from "moment";
import { toast } from "react-toastify";
import NewUserDialog from "../components/users/NewUserDialog";
import EditUserDialog from "../components/users/EditUserDialog";
import UpdatePwdDialog from "../components/users/UpdatePwdDialog";
import DeleteUserDialog from "../components/users/DeleteUserDialog";

const UserList = () => {
  const [search, setSearch] = useState("");
  const [currentPage, setCurrentPage] = useState(1);
  const [rowsPerPage, setRowsPerPage] = useState(50);
  const rowsPerPageOptions = [25, 50, 100, 200];
  const [totalRows, setTotalRows] = useState(1000);
  const [loading, setLoading] = useState(false);
  const [users, setUsers] = useState([]);
  const [isGetData, setIsGetData] = useState(false);
  const [user, setUser] = useState({});
  const [isNew, setIsNew] = useState(false);
  const [isEdit, setIsEdit] = useState(false);
  const [isPwd, setIsPwd] = useState(false);
  const [isDelete, setIsDelete] = useState(false);
  const [memberships, setMemberships] = useState([]);

  const columns = [
    {
      name: "Avatar",
      width: "75px",
      selector: (row) => (
        <UserAvatar
          theme="primary"
          text={findUpper(`${row.firstName} ${row.lastName}`)}
        ></UserAvatar>
      ),
    },
    {
      name: "First Name",
      width: "120px",
      selector: (row) => row.firstName,
    },
    {
      name: "Last Name",
      width: "120px",
      selector: (row) => row.lastName,
    },
    {
      name: "Email",
      selector: (row) => row.email,
    },
    {
      name: "Credit",
      selector: (row) => row.credit,
    },
    {
      name: "Address",
      selector: (row) => row.address && row.address,
    },
    {
      name: "Phone",
      width: "115px",
      selector: (row) => row.phone && `+${row.phone}`,
    },
    {
      name: "Membership",
      width: "170px",
      selector: (row) =>
        row.membership && (
          <div className="d-flex flex-column">
            <Badge color="primary" className="w-100">
              {row.membership.name}
            </Badge>
            <p className="mb-0">
              {moment(row.membershipStartAt).format("YYYY/MM/DD")} ~{" "}
              {moment(row.membershipEndAt).format("YYYY/MM/DD")}
            </p>
          </div>
        ),
    },
    {
      name: "Status",
      width: "100px",
      selector: (row) => (
        <Badge className="badge-dim" color={row.status ? "success" : "danger"}>
          {row.status ? "Enabled" : "Disabled"}
        </Badge>
      ),
    },
    {
      name: "Action",
      width: "160px",
      selector: (row) => (
        <div className="d-flex">
          <Button
            title="Edit"
            color="success"
            size="sm"
            outline
            className="d-flex align-items-center justify-content-center py-2 me-1"
            onClick={() => handleEditUser(row)}
          >
            <Icon icon="bx:edit" />
          </Button>
          <Button
            title="Update password"
            color="primary"
            size="sm"
            outline
            className="d-flex align-items-center justify-content-center py-2 me-1"
            onClick={() => handlePwdUser(row)}
          >
            <Icon icon="solar:key-linear" />
          </Button>
          {row.status ? (
            <Button
              title="Disable"
              color="danger"
              size="sm"
              outline
              className="d-flex align-items-center justify-content-center py-2 me-1"
              onClick={() => handleUpdateStatus(row._id, false)}
            >
              <Icon icon="fa-regular:thumbs-down" />
            </Button>
          ) : (
            <Button
              title="Enable"
              color="success"
              size="sm"
              outline
              className="d-flex align-items-center justify-content-center py-2 me-1"
              onClick={() => handleUpdateStatus(row._id, true)}
            >
              <Icon icon="fa-regular:thumbs-up" />
            </Button>
          )}
          <Button
            title="Delete"
            color="danger"
            size="sm"
            outline
            className="d-flex align-items-center justify-content-center py-2"
            onClick={() => handleDeleteUser(row)}
          >
            <Icon icon="ph:trash-bold" />
          </Button>
        </div>
      ),
    },
  ];

  useEffect(() => {
    fetchMemberships();
  }, []);

  useEffect(() => {
    fetchUsers();
  }, [search, rowsPerPage, currentPage, isGetData]);

  const fetchMemberships = async () => {
    const { data } = await Http.get("/memberships", {
      params: {
        search: "",
        page: 1,
        rowsPerPage: 100,
      },
    });

    setMemberships(data.memberships);
  };

  const fetchUsers = async () => {
    setLoading(true);
    const { data } = await Http.get("/users", {
      params: {
        search,
        page: currentPage,
        rowsPerPage,
      },
    });

    setUsers(data.users);
    setTotalRows(data.recordsFiltered);
    setLoading(false);
  };

  const handleEditUser = (row) => {
    setUser(row);
    setIsEdit(true);
  };

  const handlePwdUser = (row) => {
    setUser(row);
    setIsPwd(true);
  };

  const handleUpdateStatus = async (id, status) => {
    const { data } = await Http.put(`/users/${id}`, {
      status,
    });

    if (data.status) {
      toast.success(data.msg);
      setIsGetData((isGetData) => !isGetData);
    } else {
      toast.error(data.msg);
    }
  };

  const handleDeleteUser = (row) => {
    setUser(row);
    setIsDelete(true);
  };

  return (
    <Card>
      <CardHeader className="p-3 d-flex align-items-center justify-content-between bg-white">
        <h4 className="mb-0">User Management</h4>
        <Button
          color="primary"
          size="sm"
          className="d-flex align-items-center gap-2"
          onClick={() => setIsNew(true)}
        >
          <Icon icon="mdi:register-outline" fontSize={20} /> New User
        </Button>
      </CardHeader>
      <div className="dataTables_wrapper dt-bootstrap4 no-footer">
        <Row className="border-bottom justify-between p-3 g-2">
          <Col className="col-7 text-start" lg="3" sm="4">
            <div className="dataTables_filter">
              <InputGroup>
                <InputGroupText>
                  <Icon icon="ri:search-line" />
                </InputGroupText>
                <Input
                  type="search"
                  placeholder="Search..."
                  value={search}
                  onChange={(ev) => setSearch(ev.target.value)}
                />
              </InputGroup>
            </div>
          </Col>
          <Col className="col-5 text-end" lg="9" sm="8">
            <div className="datatable-filter">
              <div className="dataTables_length">
                <Label className="mb-0">
                  <Input
                    type="select"
                    value={rowsPerPage}
                    onChange={(ev) => setRowsPerPage(ev.target.value)}
                  >
                    {rowsPerPageOptions.map((rowsPerPageOption, idx) => (
                      <option key={idx} value={rowsPerPageOption}>
                        {rowsPerPageOption}
                      </option>
                    ))}
                  </Input>
                </Label>
              </div>
            </div>
          </Col>
        </Row>
        <DataTable
          className="border-bottom"
          columns={columns}
          data={users}
          pagination
          paginationServer
          paginationTotalRows={totalRows}
          paginationPerPage={rowsPerPage}
          paginationRowsPerPageOptions={rowsPerPageOptions}
          paginationComponent={({ rowCount, onChangeRowsPerPage }) => (
            <DataTablePagination
              currentPage={currentPage}
              rowsPerPage={rowsPerPage}
              rowCount={rowCount}
              onChangePage={setCurrentPage}
            />
          )}
          progressPending={loading}
          progressComponent={
            <div
              className="d-flex justify-content-center align-items-center"
              style={{ minHeight: 100 }}
            >
              <Spinner size="sm" className="me-1" />{" "}
              <span>Please wait for a moment...</span>
            </div>
          }
          sortIcon={
            <div>
              <span>&darr;</span>
              <span>&uarr;</span>
            </div>
          }
          customStyles={{
            headCells: {
              style: {
                paddingLeft: "12px !important",
                paddingRight: "5px !important",
              },
            },
            cells: {
              style: {
                paddingLeft: "12px !important",
                paddingRight: "5px !important",
              },
            },
          }}
        />
      </div>
      <NewUserDialog
        open={isNew}
        memberships={memberships}
        onSuccess={() => setIsGetData((isGetData) => !isGetData)}
        onClose={() => setIsNew(false)}
      />
      <EditUserDialog
        open={isEdit}
        user={user}
        memberships={memberships}
        onSuccess={() => setIsGetData((isGetData) => !isGetData)}
        onClose={() => setIsEdit(false)}
      />
      <UpdatePwdDialog
        open={isPwd}
        user={user}
        onClose={() => setIsPwd(false)}
      />
      <DeleteUserDialog
        open={isDelete}
        user={user}
        onSuccess={() => setIsGetData((isGetData) => !isGetData)}
        onClose={() => setIsDelete(false)}
      />
    </Card>
  );
};

export default UserList;
