import { useState, useEffect } from "react";
import {
  Modal,
  ModalHeader,
  ModalBody,
  ModalFooter,
  FormGroup,
  Label,
  InputGroup,
  InputGroupText,
  Input,
  Button,
  Spinner,
} from "reactstrap";
import { toast } from "react-toastify";
import { Icon } from "@iconify/react";
import Http from "../../services/Http";

const UpdatePwdDialog = ({ open, user, onClose }) => {
  const [loading, setLoading] = useState(false);
  const [password, setPassword] = useState("");
  const [confirmPwd, setConfirmPwd] = useState("");

  useEffect(() => {
    if (open) {
      setPassword("");
      setConfirmPwd("");
    }
  }, [open]);

  const handleUpdatePassword = async () => {
    if (password.trim() === "") {
      toast.error("Please enter a password.");
    } else if (password.trim() !== confirmPwd.trim()) {
      toast.error("Passwords don't match.");
    } else {
      try {
        setLoading(true);
        const id = user._id;
        const { data } = await Http.put(`/users/${id}`, {
          password,
        });
        if (data.status) {
          toast.success(data.msg);
          onClose();
        } else {
          toast.error(data.msg);
        }
      } catch (err) {
        toast.error(err.message);
      } finally {
        setTimeout(() => setLoading(false), 500);
      }
    }
  };

  return (
    <Modal isOpen={open} className="modal-dialog-centered">
      <ModalHeader toggle={onClose}>Update Password</ModalHeader>
      <ModalBody>
        <FormGroup className="required">
          <Label>New Password</Label>
          <InputGroup>
            <InputGroupText>
              <Icon icon="ph:lock-bold" />
            </InputGroupText>
            <Input
              type="password"
              value={password}
              onChange={(ev) => setPassword(ev.target.value)}
              autoComplete="new-password"
            />
          </InputGroup>
        </FormGroup>
        <div className="required">
          <Label>Password Confirmation</Label>
          <InputGroup>
            <InputGroupText>
              <Icon icon="mdi:lock-check-outline" />
            </InputGroupText>
            <Input
              type="password"
              value={confirmPwd}
              onChange={(ev) => setConfirmPwd(ev.target.value)}
              autoComplete="new-password"
            />
          </InputGroup>
        </div>
      </ModalBody>
      <ModalFooter>
        <Button
          color="primary"
          className="d-flex align-items-center gap-2"
          onClick={handleUpdatePassword}
        >
          {loading ? (
            <Spinner size="sm" className="me-1" />
          ) : (
            <Icon icon="material-symbols:save-outline" />
          )}{" "}
          Update
        </Button>
        <Button
          type="button"
          color="danger"
          className="d-flex align-items-center gap-2"
          onClick={onClose}
        >
          <Icon icon="mdi:close" />
          Cancel
        </Button>
      </ModalFooter>
    </Modal>
  );
};

export default UpdatePwdDialog;
