import { useState, useEffect } from "react";
import {
  Modal,
  ModalHeader,
  ModalBody,
  ModalFooter,
  Row,
  Col,
  FormGroup,
  Label,
  Input,
  Button,
  Spinner,
} from "reactstrap";
import { Icon } from "@iconify/react";
import Http from "../../services/Http";
import { toast } from "react-toastify";

const NewMembershipDialog = ({ open, onSuccess, onClose }) => {
  const [loading, setLoading] = useState(false);
  const [name, setName] = useState("");
  const [type, setType] = useState("");
  const [credit, setCredit] = useState(0);
  const [price, setPrice] = useState(0);
  const [description, setDescription] = useState("");

  useEffect(() => {
    if (open) {
      setLoading(false);
      setName("");
      setType("monthly");
      setCredit(0);
      setPrice(0);
      setDescription("");
    }
  }, [open]);

  const handleCreateMembership = async () => {
    if (name.trim() === "") {
      toast.error("Please enter a membership name.");
    } else if (credit <= 0) {
      toast.error("Please enter the number of credit.");
    } else if (price <= 0) {
      toast.error("Please enter a membership price.");
    } else {
      try {
        setLoading(true);
        const { data } = await Http.post("/memberships", {
          name,
          type,
          credit,
          price,
          description,
        });
        if (data.status) {
          toast.success(data.msg);
          onSuccess();
          onClose();
        } else {
          toast.error(data.msg);
        }
      } catch (err) {
        toast.error(err.message);
      } finally {
        setLoading(false);
      }
    }
  };

  return (
    <Modal isOpen={open} className="modal-dialog-centered">
      <ModalHeader toggle={onClose}>New Membership</ModalHeader>
      <ModalBody>
        <FormGroup className="required">
          <Label>Name</Label>
          <Input
            type="text"
            value={name}
            onChange={(ev) => setName(ev.target.value)}
          />
        </FormGroup>
        <FormGroup className="required">
          <Label>Type</Label>
          <Input
            type="select"
            value={type}
            onChange={(ev) => setType(ev.target.value)}
          >
            <option value="monthly">Monthly</option>
            <option value="yearly">Yearly</option>
          </Input>
        </FormGroup>
        <Row>
          <Col sm={6}>
            <FormGroup className="required">
              <Label>Credit</Label>
              <Input
                type="number"
                value={credit}
                onChange={(ev) => setCredit(ev.target.value)}
              />
            </FormGroup>
          </Col>
          <Col sm={6}>
            <FormGroup className="required">
              <Label>Price</Label>
              <Input
                type="number"
                value={price}
                onChange={(ev) => setPrice(ev.target.value)}
              />
            </FormGroup>
          </Col>
        </Row>
        <div>
          <Label>Description</Label>
          <Input
            type="textarea"
            rows={5}
            value={description}
            onChange={(ev) => setDescription(ev.target.value)}
          />
        </div>
      </ModalBody>
      <ModalFooter>
        <Button
          color="primary"
          className="d-flex align-items-center gap-2"
          onClick={handleCreateMembership}
        >
          {loading ? (
            <Spinner size="sm" className="me-1" />
          ) : (
            <Icon icon="material-symbols:save-outline" />
          )}{" "}
          Create
        </Button>
        <Button
          type="button"
          color="danger"
          className="d-flex align-items-center gap-2"
          onClick={onClose}
        >
          <Icon icon="mdi:close" />
          Cancel
        </Button>
      </ModalFooter>
    </Modal>
  );
};

export default NewMembershipDialog;
