import { useState, useEffect } from "react";
import {
  Modal,
  ModalBody,
  Button,
  Spinner,
} from "reactstrap";
import { Icon } from "@iconify/react";
import { toast } from "react-toastify";
import Http from "../../services/Http";

const DeleteUserDialog = ({ open, user, onSuccess, onClose }) => {
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    open && setLoading(false);
  }, [open]);

  const handleDeleteUser = async () => {
    try {
      setLoading(true);
      const id = user._id;
      const { data } = await Http.delete(`/users/${id}`);
      if (data.status) {
        toast.success(data.msg)
        onSuccess()
        onClose()
      } else {
        toast.error(data.msg)
      }
    } catch (err) {
      toast.error(err.message);
    } finally {
      setTimeout(() => setLoading(false), 500);
    }
  };

  return (
    <Modal isOpen={open} size="md" className="modal-dialog-centered">
      <ModalBody
        className="text-center"
        style={{ paddingTop: "2rem", paddingBottom: "2rem" }}
      >
        <h4>Do you really want to delete this user?</h4>
        <p>Once you delete this user, you can not revert it again.</p>
        <div className="d-flex align-items-center justify-content-center gap-3 mt-4">
          <Button
            color="primary"
            className="d-flex align-items-center justify-content-center gap-2"
            style={{ minWidth: 150 }}
            disabled={loading}
            onClick={handleDeleteUser}
          >
            {loading ? (
              <Spinner size="sm" color="light" />
            ) : (
              <Icon icon="ph:trash-bold" />
            )}
            Delete
          </Button>
          <Button
            type="button"
            className="d-flex align-items-center justify-content-center gap-2"
            style={{ minWidth: 150 }}
            color="danger"
            onClick={onClose}
          >
            <Icon icon="mdi:close" /> Cancel
          </Button>
        </div>
      </ModalBody>
    </Modal>
  );
};

export default DeleteUserDialog;
