import { Fragment, useState, useEffect } from "react";
import {
  Card,
  CardHeader,
  CardBody,
  CardFooter,
  Row,
  Col,
  FormGroup,
  Label,
  Input,
  InputGroup,
  Button,
  Spinner,
  Alert,
} from "reactstrap";
import { Icon } from "@iconify/react";
import { toast } from "react-toastify";
import Http from "../services/Http";

const Setting = () => {
  const [loading, setLoading] = useState(false);
  const [isGetData, setIsGetData] = useState(false);
  const [indexmenowEmail, setIndexmenowEmail] = useState("");
  const [indexmenowPassword, setIndexmenowPassword] = useState("");
  const [indexmenowApiKey, setIndexmenowApiKey] = useState("");
  const [indexmenowLinkLimit, setIndexmenowLinkLimit] = useState(5);
  const [sendgridApiKey, setSendgridApiKey] = useState("");
  const [sendgridUser, setSendgridUser] = useState("");
  const [loginLoading, setLoginLoading] = useState(false);
  const [indexmenowCookie, setIndexmenowCookie] = useState("");
  const [paypalAccountType, setPaypalAccountType] = useState("sandbox");
  const [paypalClientID, setPaypalClientID] = useState("");
  const [paypalSecretKey, setPaypalSecretKey] = useState("");

  useEffect(() => {
    fetchSettings();
  }, [isGetData]);

  const fetchSettings = async () => {
    const { data } = await Http.get("/settings");
    if (data) {
      setIndexmenowEmail(data?.indexmenowEmail);
      setIndexmenowPassword(data?.indexmenowPassword);
      setIndexmenowApiKey(data?.indexmenowApiKey);
      setIndexmenowLinkLimit(
        data.indexmenowLinkLimit ? data.indexmenowLinkLimit : 5
      );
      setIndexmenowCookie(data?.indexmenowCookie);
      setSendgridApiKey(data?.sendgridApiKey);
      setSendgridUser(data?.sendgridUser);
      setPaypalAccountType(
        data.paypalAccountType ? data.paypalAccountType : "sandbox"
      );
      setPaypalClientID(data?.paypalClientID);
      setPaypalSecretKey(data?.paypalSecretKey);
    }
  };

  const handleUpdateSetting = async () => {
    if (indexmenowEmail.trim() === "") {
      toast.error("Please enter a email for indexmenow.");
    } else if (indexmenowPassword.trim() === "") {
      toast.error("Please enter a password for indexmenow.");
    } else if (indexmenowApiKey.trim() === "") {
      toast.error("Please enter an api key for indexmenow.");
    } else if (sendgridApiKey.trim() === "") {
      toast.error("Please enter an api key for sendgrid.");
    } else if (sendgridUser.trim() === "") {
      toast.error("Please enter a user for sendgrid.");
    } else if (paypalClientID.trim() === "") {
      toast.error("Please enter a paypal client id.");
    } else if (paypalSecretKey.trim() === "") {
      toast.error("Please enter a paypal secret key.");
    } else {
      try {
        setLoading(true);

        const { data } = await Http.post("/settings", {
          indexmenowEmail,
          indexmenowPassword,
          indexmenowApiKey,
          indexmenowLinkLimit,
          sendgridApiKey,
          sendgridUser,
          paypalAccountType,
          paypalClientID,
          paypalSecretKey,
        });
        if (data.status) {
          toast.success(data.msg);
          setIsGetData((isGetData) => !isGetData);
        } else {
          toast.error(data.msg);
        }
      } catch (err) {
        toast.error(err.message);
      } finally {
        setTimeout(() => setLoading(false), 500);
      }
    }
  };

  const handleLoginToIndexmenow = async () => {
    if (indexmenowEmail && indexmenowPassword) {
      try {
        setLoginLoading(true);
        const { data } = await Http.post("/settings/login", {
          email: indexmenowEmail,
          password: indexmenowPassword,
        });
        if (data.status) {
          toast.success(data.msg);
          setIsGetData((isGetData) => !isGetData);
        } else {
          toast.error(data.msg);
        }
      } catch (err) {
        toast.error(err.message);
      } finally {
        setLoginLoading(false);
      }
    } else {
      toast.error("Please set email and password for indexmenow before login.");
    }
  };

  return (
    <Fragment>
      <Row>
        <Col lg={6} xs={12}>
          <Card className="mb-lg-0 mb-3">
            <CardHeader className="p-3 bg-white">
              <h4 className="mb-0">Setting Management</h4>
            </CardHeader>
            <CardBody className="px-4">
              <Row>
                <Col md={6} sm={12}>
                  <FormGroup className="required">
                    <Label>Indexmenow Email</Label>
                    <Input
                      type="text"
                      value={indexmenowEmail}
                      onChange={(ev) => setIndexmenowEmail(ev.target.value)}
                    />
                  </FormGroup>
                </Col>
                <Col md={6} sm={12}>
                  <FormGroup className="required">
                    <Label>Indexmenow Password</Label>
                    <Input
                      type="text"
                      value={indexmenowPassword}
                      onChange={(ev) => setIndexmenowPassword(ev.target.value)}
                    />
                  </FormGroup>
                </Col>
                <Col md={6} sm={12}>
                  <FormGroup className="required">
                    <Label>Indexmenow Api Key</Label>
                    <Input
                      type="text"
                      value={indexmenowApiKey}
                      onChange={(ev) => setIndexmenowApiKey(ev.target.value)}
                    />
                  </FormGroup>
                </Col>
                <Col md={6} sm={12}>
                  <FormGroup className="required">
                    <Label>Indexmenow Link Limit</Label>
                    <Input
                      type="number"
                      value={indexmenowLinkLimit}
                      onChange={(ev) => setIndexmenowLinkLimit(ev.target.value)}
                    />
                  </FormGroup>
                </Col>
                <Col md={6} sm={12}>
                  <FormGroup className="required">
                    <Label>Sendgrid Api Key</Label>
                    <Input
                      type="text"
                      value={sendgridApiKey}
                      onChange={(ev) => setSendgridApiKey(ev.target.value)}
                    />
                  </FormGroup>
                </Col>
                <Col md={6} sm={12}>
                  <FormGroup className="required">
                    <Label>Sendgrid User</Label>
                    <Input
                      type="email"
                      value={sendgridUser}
                      onChange={(ev) => setSendgridUser(ev.target.value)}
                    />
                  </FormGroup>
                </Col>
                <Col sm={12}>
                  <FormGroup className="required">
                    <Label>PayPal Account Type</Label>
                    <Input
                      type="select"
                      value={paypalAccountType}
                      onChange={(ev) => setPaypalAccountType(ev.target.value)}
                    >
                      <option value="sandbox">Sandbox</option>
                      <option value="live">Live</option>
                    </Input>
                  </FormGroup>
                </Col>
                <Col sm={12}>
                  <FormGroup className="required">
                    <Label>PayPal Client ID</Label>
                    <Input
                      type="text"
                      value={paypalClientID}
                      onChange={(ev) => setPaypalClientID(ev.target.value)}
                    />
                  </FormGroup>
                </Col>
                <Col sm={12}>
                  <div className="mb-1 required">
                    <Label>PayPal Secret Key</Label>
                    <Input
                      type="text"
                      value={paypalSecretKey}
                      onChange={(ev) => setPaypalSecretKey(ev.target.value)}
                    />
                  </div>
                </Col>
              </Row>
            </CardBody>
            <CardFooter className="d-flex justify-content-end bg-white py-3">
              <Button
                color="primary"
                disabled={loading}
                className="d-flex align-items-center gap-2"
                onClick={handleUpdateSetting}
              >
                {loading ? (
                  <Spinner size="sm" />
                ) : (
                  <Icon icon="material-symbols:save-outline" fontSize={20} />
                )}{" "}
                Update
              </Button>
            </CardFooter>
          </Card>
        </Col>
        <Col lg={6} xs={12}>
          <Card className="mb-3">
            <CardHeader className="p-3 bg-white">
              <h4 className="mb-0">Indexmenow Login</h4>
            </CardHeader>
            <CardBody className="p-4">
              <Alert color="info">
                <p className="mb-2">
                  Please login into indexmenow.com if your application lose
                  cookie.
                </p>
                <p className="mb-1">
                  <strong>
                    <span className="d-inline-block" style={{ minWidth: 90 }}>Email:</span>{" "}
                    {indexmenowEmail ? indexmenowEmail : "No set"}
                  </strong>
                </p>
                <p className="mb-1">
                  <strong>
                    <span className="d-inline-block" style={{ minWidth: 90 }}>Password:</span>{" "}
                    {indexmenowPassword ? indexmenowPassword : "No set"}
                  </strong>
                </p>
              </Alert>
              <InputGroup>
                <Input type="text" readOnly value={indexmenowCookie} />
                <Button
                  color="primary"
                  disabled={loginLoading}
                  className="d-flex align-items-center gap-2"
                  outline
                  onClick={handleLoginToIndexmenow}
                >
                  {loginLoading ? (
                    <Spinner size="sm" />
                  ) : (
                    <Icon icon="mdi:sign-in" />
                  )}{" "}
                  Login
                </Button>
              </InputGroup>
            </CardBody>
          </Card>
        </Col>
      </Row>
    </Fragment>
  );
};

export default Setting;
