import { useState, useEffect, forwardRef } from "react";
import {
  Modal,
  ModalHeader,
  ModalBody,
  ModalFooter,
  Row,
  Col,
  FormGroup,
  Label,
  InputGroup,
  InputGroupText,
  Input,
  Button,
  Spinner,
} from "reactstrap";
import PhoneInput from "react-phone-input-2";
import DatePicker from "react-datepicker";
import { toast } from "react-toastify";
import { Icon } from "@iconify/react";
import Http from "../../services/Http";
import moment from "moment";
import "react-phone-input-2/lib/style.css";
import "react-datepicker/dist/react-datepicker.css";

const CustomInput = forwardRef(({ ...props }, ref) => {
  return (
    <InputGroup>
      <InputGroupText>
        <Icon icon="mdi:calendar" />
      </InputGroupText>
      <Input ref={ref} {...props} />
    </InputGroup>
  );
});

const EditUserDialog = ({ open, user, memberships, onSuccess, onClose }) => {
  const [loading, setLoading] = useState(false);
  const [firstName, setFirstName] = useState("");
  const [lastName, setLastName] = useState("");
  const [email, setEmail] = useState("");
  const [phone, setPhone] = useState("");
  const [address, setAddress] = useState("");
  const [membership, setMembership] = useState("");
  const [membershipStartAt, setMembershipStartAt] = useState(new Date());
  const [membershipEndAt, setMembershipEndAt] = useState(
    new Date(moment().add(1, "months").format("YYYY-MM-DD HH:mm:ss"))
  );

  useEffect(() => {
    if (open) {
      setLoading(false);
      setFirstName(user.firstName);
      setLastName(user.lastName);
      setEmail(user.email);
      setPhone(user.phone);
      setAddress(user.address);
      setMembership(user.membership ? user.membership._id : "");
      setMembershipStartAt(
        user.membershipStartAt ? new Date(user.membershipStartAt) : new Date()
      );
      setMembershipEndAt(
        user.membershipEndAt
          ? new Date(user.membershipEndAt)
          : new Date(moment().add(1, "months"))
      );
    }
  }, [open]);

  const handleUpdateUser = async () => {
    if (firstName.trim() === "") {
      toast.error("Please enter a first name.");
    } else if (lastName.trim() === "") {
      toast.error("Please enter a last name.");
    } else if (email.trim() === "") {
      toast.error("Please enter an email address.");
    } else if (!/^[\w-\.]+@([\w-]+\.)+[\w-]{2,4}$/.test(email)) {
      toast.error("Please enter a valid email.");
    } else if (membership.trim() === "") {
      toast.error("Please choose a membership.");
    } else {
      try {
        setLoading(true);
        const id = user._id;
        const { data } = await Http.put(`/users/${id}`, {
          firstName,
          lastName,
          email,
          phone,
          address,
          membership,
          membershipStartAt: moment(membershipStartAt).format(
            "YYYY-MM-DD HH:mm:ss"
          ),
          membershipEndAt: moment(membershipEndAt).format(
            "YYYY-MM-DD HH:mm:ss"
          ),
        });

        if (data.status) {
          toast.success(data.msg);
          onSuccess();
          onClose();
        } else {
          toast.error(data.msg);
        }
      } catch (err) {
        toast.error(err.message);
      } finally {
        setTimeout(() => setLoading(false), 500);
      }
    }
  };

  return (
    <Modal
      isOpen={open}
      size="lg"
      className="modal-dialog-centered"
      style={{ maxWidth: 600 }}
    >
      <ModalHeader toggle={onClose}>Edit User</ModalHeader>
      <ModalBody>
        <Row>
          <Col md={6} xs={12}>
            <FormGroup className="required">
              <Label>First Name</Label>
              <InputGroup>
                <InputGroupText>
                  <Icon icon="icon-park-outline:edit-name" />
                </InputGroupText>
                <Input
                  type="text"
                  value={firstName}
                  onChange={(ev) => setFirstName(ev.target.value)}
                />
              </InputGroup>
            </FormGroup>
          </Col>
          <Col md={6} xs={12}>
            <FormGroup className="required">
              <Label>Last Name</Label>
              <InputGroup>
                <InputGroupText>
                  <Icon icon="icon-park-outline:edit-name" />
                </InputGroupText>
                <Input
                  type="text"
                  value={lastName}
                  onChange={(ev) => setLastName(ev.target.value)}
                />
              </InputGroup>
            </FormGroup>
          </Col>
          <Col md={6} xs={12}>
            <FormGroup className="required">
              <Label>Email</Label>
              <InputGroup>
                <InputGroupText>
                  <Icon icon="ic:outline-email" />
                </InputGroupText>
                <Input
                  type="email"
                  value={email}
                  onChange={(ev) => setEmail(ev.target.value)}
                />
              </InputGroup>
            </FormGroup>
          </Col>
          <Col md={6} xs={12}>
            <FormGroup>
              <Label>Phone</Label>
              <PhoneInput
                spacialLabel=""
                country="us"
                value={phone}
                onChange={(phone) => setPhone(phone)}
                inputStyle={{ width: "100%", height: 37 }}
              />
            </FormGroup>
          </Col>
          <Col xs={12}>
            <FormGroup>
              <Label>Address</Label>
              <InputGroup>
                <InputGroupText>
                  <Icon icon="ph:address-book" />
                </InputGroupText>
                <Input
                  type="text"
                  value={address}
                  onChange={(ev) => setAddress(ev.target.value)}
                />
              </InputGroup>
            </FormGroup>
          </Col>
          <Col xs={12}>
            <FormGroup className="required">
              <Label>Membership</Label>
              <InputGroup>
                <InputGroupText>
                  <Icon icon="material-symbols:card-membership" />
                </InputGroupText>
                <Input
                  type="select"
                  value={membership}
                  onChange={(ev) => setMembership(ev.target.value)}
                >
                  <option value=""></option>
                  {memberships.map((membership, idx) => (
                    <option key={idx} value={membership._id}>{membership.name}</option>
                  ))}
                </Input>
              </InputGroup>
            </FormGroup>
          </Col>
          <Col md={6} xs={12}>
            <div className="mb-2">
              <Label>Membership Start At</Label>
              <DatePicker
                showTimeSelect
                dateFormat="yyyy/MM/dd HH:mm:ss"
                placeholderText="YYYY/MM/DD HH:mm:ss"
                selected={membershipStartAt}
                onChange={(date) => setMembershipStartAt(date)}
                customInput={<CustomInput />}
              />
            </div>
          </Col>
          <Col md={6} xs={12}>
            <FormGroup>
              <Label>Membership End At</Label>
              <DatePicker
                showTimeSelect
                dateFormat="yyyy/MM/dd HH:mm:ss"
                placeholderText="YYYY/MM/DD HH:mm:ss"
                selected={membershipEndAt}
                onChange={(date) => setMembershipEndAt(date)}
                customInput={<CustomInput />}
              />
            </FormGroup>
          </Col>
        </Row>
      </ModalBody>
      <ModalFooter>
        <Button
          color="primary"
          className="d-flex align-items-center gap-2"
          onClick={handleUpdateUser}
        >
          {loading ? (
            <Spinner size="sm" className="me-1" />
          ) : (
            <Icon icon="material-symbols:save-outline" />
          )}{" "}
          Update
        </Button>
        <Button
          type="button"
          color="danger"
          className="d-flex align-items-center gap-2"
          onClick={onClose}
        >
          <Icon icon="mdi:close" />
          Cancel
        </Button>
      </ModalFooter>
    </Modal>
  );
};

export default EditUserDialog;
