import { Modal } from "reactstrap";
import { Nav, NavItem } from "reactstrap";
import { Link, useLocation } from "react-router-dom";
import { Icon } from "@iconify/react";
import useAuth from "../hooks/useAuth";
import menu from "./menu";

const Drawer = ({ open, onClose }) => {
  const { user } = useAuth();
  const location = useLocation();

  return (
    <Modal
      isOpen={open}
      toggle={onClose}
      fullscreen
      className="imn-drawer-container"
    >
      <div className="imn-drawer">
        <div className="imn-drawer-head">
          <Link
            to={user.role === "admin" ? "/admin" : "/"}
            className="text-decoration-none"
          >
            <Link to={"/"} className="text-decoration-none">
              <div className="ps-4 mt-1 mb-2">
                <img src="/logo.png" height={52} alt="logo" />
              </div>
            </Link>
          </Link>
        </div>
        <Nav vertical className="py-2">
          {menu.map((item, idx) => (
            <NavItem key={idx}>
              <Link
                to={item.link}
                className={`nav-link ${
                  location.pathname === item.link && "active"
                }`}
              >
                <Icon icon={item.icon} /> {item.text}
              </Link>
            </NavItem>
          ))}
        </Nav>
      </div>
    </Modal>
  );
};

export default Drawer;
