import { useState, useEffect } from "react";
import {
  Card,
  CardHeader,
  Button,
  Row,
  Col,
  InputGroup,
  InputGroupText,
  Input,
  Spinner,
} from "reactstrap";
import { Icon } from "@iconify/react";
import DataTable from "react-data-table-component";
import DataTablePagination from "../components/DataTablePagination";
import NewMembershipDialog from "../components/memberships/NewMembershipDialog";
import EditMembershipDialog from "../components/memberships/EditMembershipDialog";
import DeleteMembershipDialog from "../components/memberships/DeleteMembershipDialog";
import Http from "../services/Http";

const MembershipList = () => {
  const [search, setSearch] = useState("");
  const [currentPage, setCurrentPage] = useState(1);
  const [rowsPerPage, setRowsPerPage] = useState(50);
  const rowsPerPageOptions = [25, 50, 100, 200];
  const [totalRows, setTotalRows] = useState(1000);
  const [loading, setLoading] = useState(false);
  const [memberships, setMemberships] = useState([]);
  const [membership, setMembership] = useState({});
  const [isGetData, setIsGetData] = useState(false);
  const [isNew, setIsNew] = useState(false);
  const [isEdit, setIsEdit] = useState(false);
  const [isDelete, setIsDelete] = useState(false);
  const columns = [
    {
      name: "Name",
      width: "200px",
      selector: (row) => row.name,
    },
    {
      name: "Credit",
      width: "150px",
      selector: row => row.credit
    },
    {
      name: "Price",
      width: "150px",
      selector: (row) => `$${row.price.toFixed(2)}`,
    },
    {
      name: "Type",
      width: "150px",
      selector: (row) => (row.type === "monthly" ? "Monthly" : "Annually"),
    },
    {
      name: "Description",
      selector: (row) => (
        <pre
          style={{
            display: "-webkit-box",
            overflow: "hidden",
            height: 80,
            WebkitLineClamp: 6,
            WebkitBoxOrient: "vertical",
            marginBottom: 0,
          }}
        >
          {row.description}
        </pre>
      ),
    },
    {
      name: "Action",
      width: "90px",
      selector: (row) => (
        <div className="d-flex">
          <Button
            title="Edit"
            color="success"
            size="sm"
            outline
            className="d-flex align-items-center justify-content-center py-2 me-1"
            onClick={() => handleEditMembership(row)}
          >
            <Icon icon="bx:edit" />
          </Button>
          <Button
            title="Delete"
            color="danger"
            size="sm"
            outline
            className="d-flex align-items-center justify-content-center py-2"
            onClick={() => handleDeleteMembership(row)}
          >
            <Icon icon="ph:trash-bold" />
          </Button>
        </div>
      ),
    },
  ];

  useEffect(() => {
    fetchMemberships();
  }, [search, rowsPerPage, currentPage, isGetData]);

  const fetchMemberships = async () => {
    setLoading(true);
    const { data } = await Http.get("/memberships", {
      params: {
        search,
        page: currentPage,
        rowsPerPage,
      },
    });
    setMemberships(data.memberships);
    setTotalRows(data.recordsFiltered);
    setLoading(false);
  };

  const handleEditMembership = (row) => {
    setMembership(row);
    setIsEdit(true);
  };

  const handleDeleteMembership = (row) => {
    setMembership(row);
    setIsDelete(true);
  };

  return (
    <Card>
      <CardHeader className="d-flex align-items-center justify-content-between py-3 bg-white">
        <h4 className="mb-0">Membership Management</h4>
        <Button
          color="primary"
          size='sm'
          className="d-flex align-items-center gap-2"
          onClick={() => setIsNew(true)}
        >
          <Icon icon="mdi:plus" fontSize={20} /> New Membership
        </Button>
      </CardHeader>
      <div className="dataTables_wrapper dt-bootstrap4 no-footer">
        <Row className="border-bottom justify-between p-3 g-2">
          <Col className="col-7 text-start" lg="3" sm="4">
            <div className="dataTables_filter">
              <InputGroup>
                <InputGroupText>
                  <Icon icon="ri:search-line" />
                </InputGroupText>
                <Input
                  type="search"
                  placeholder="Search..."
                  value={search}
                  onChange={(ev) => setSearch(ev.target.value)}
                />
              </InputGroup>
            </div>
          </Col>
          <Col className="col-5 text-end" lg="9" sm="8">
            <div className="datatable-filter">
              <div className="dataTables_length">
                <label className="mb-0">
                  <Input
                    type="select"
                    value={rowsPerPage}
                    onChange={(ev) => setRowsPerPage(ev.target.value)}
                  >
                    {rowsPerPageOptions.map((rowsPerPageOption, idx) => (
                      <option key={idx} value={rowsPerPageOption}>
                        {rowsPerPageOption}
                      </option>
                    ))}
                  </Input>
                </label>
              </div>
            </div>
          </Col>
        </Row>
        <DataTable
          className="border-bottom"
          columns={columns}
          data={memberships}
          pagination
          paginationServer
          paginationTotalRows={totalRows}
          paginationPerPage={rowsPerPage}
          paginationRowsPerPageOptions={rowsPerPageOptions}
          paginationComponent={({ rowCount, onChangeRowsPerPage }) => (
            <DataTablePagination
              currentPage={currentPage}
              rowsPerPage={rowsPerPage}
              rowCount={rowCount}
              onChangePage={setCurrentPage}
            />
          )}
          progressPending={loading}
          progressComponent={
            <div
              className="d-flex justify-content-center align-items-center"
              style={{ minHeight: 100 }}
            >
              <Spinner size="sm" className="me-1" />{" "}
              <span>Please wait for a moment...</span>
            </div>
          }
          sortIcon={
            <div>
              <span>&darr;</span>
              <span>&uarr;</span>
            </div>
          }
          customStyles={{
            headCells: {
              style: {
                paddingLeft: "12px !important",
                paddingRight: "5px !important",
              },
            },
            cells: {
              style: {
                paddingLeft: "12px !important",
                paddingRight: "5px !important",
              },
            },
          }}
        />
      </div>
      <NewMembershipDialog
        open={isNew}
        onSuccess={() => setIsGetData((isGetData) => !isGetData)}
        onClose={() => setIsNew(false)}
      />
      <EditMembershipDialog
        open={isEdit}
        membership={membership}
        onSuccess={() => setIsGetData(isGetData => !isGetData)}
        onClose={() => setIsEdit(false)}
      />
      <DeleteMembershipDialog
        open={isDelete}
        membership={membership}
        onSuccess={() => setIsGetData(isGetData => !isGetData)}
        onClose={() => setIsDelete(false)}
      />
    </Card>
  );
};

export default MembershipList;
