import { Fragment, useState, useEffect } from "react";
import {
  Card,
  CardHeader,
  CardBody,
  CardFooter,
  Row,
  Col,
  FormGroup,
  InputGroup,
  InputGroupText,
  Input,
  Label,
  Button,
  Spinner,
} from "reactstrap";
import Http from "../services/Http";
import useAuth from "../hooks/useAuth";
import { Icon } from "@iconify/react";
import PhoneInput from "react-phone-input-2";
import { toast } from "react-toastify";
import "react-phone-input-2/lib/style.css";

const Profile = () => {
  const { user, setUser } = useAuth();
  const [profileLoading, setProfileLoading] = useState(false);
  const [passwordLoading, setPasswordLoading] = useState(false);
  const [firstName, setFirstName] = useState("");
  const [lastName, setLastName] = useState("");
  const [email, setEmail] = useState("");
  const [phone, setPhone] = useState("");
  const [address, setAddress] = useState("");
  const [prevPwd, setPrevPwd] = useState("");
  const [password, setPassword] = useState("");
  const [confirmPwd, setConfirmPwd] = useState("");

  useEffect(() => {
    setProfileLoading(false);
    setPasswordLoading(false);
    setFirstName(user?.firstName);
    setLastName(user?.lastName);
    setEmail(user?.email);
    setPhone(user?.phone);
    setAddress(user?.address);
    setPrevPwd("");
    setPassword("");
    setConfirmPwd("");
  }, []);

  const handleUpdateProfile = async () => {
    if (firstName.trim() === "") {
      toast.error("Please enter a first name.");
    } else if (lastName.trim() === "") {
      toast.error("Please enter a last name.");
    } else if (email.trim() === "") {
      toast.error("Please enter an email address.");
    } else if (!/^[\w-\.]+@([\w-]+\.)+[\w-]{2,4}$/.test(email)) {
      toast.error("Please enter a valid email.");
    } else {
      try {
        setProfileLoading(true);
        const { data } = await Http.put(`/profile/${user._id}/update`, {
          firstName,
          lastName,
          email,
          phone,
        });

        if (data.status) {
          setUser(data.user);
          toast.success(data.msg);
        } else {
          toast.error(data.msg);
        }
      } catch (err) {
        toast.error(err.message);
      } finally {
        setTimeout(() => setProfileLoading(false), 500);
      }
    }
  };

  const handleUpdatePassword = async () => {
    if (prevPwd.trim() === "") {
      toast.error("Please enter a current password.");
    } else if (password.trim() === "") {
      toast.error("Please enter a new password.");
    } else if (password.trim() !== confirmPwd.trim()) {
      toast.error("Passwords don't match.");
    } else {
      try {
        setPasswordLoading(true);
        const { data } = await Http.put(`/profile/${user._id}/password`, {
          prevPassword: prevPwd,
          password,
        });

        if (data.status) {
          toast.success(data.msg);
          setPrevPwd("");
          setPassword("");
          setConfirmPwd("");
        } else {
          toast.error(data.msg);
        }
      } catch (err) {
        toast.error(err.message);
      } finally {
        setTimeout(() => setPasswordLoading(false), 500);
      }
    }
  };

  return (
    <Fragment>
      <Row>
        <Col lg={6} xs={12}>
          <Card className="mb-3">
            <CardHeader className="bg-white p-3">
              <h4 className="mb-0">Personal Information</h4>
            </CardHeader>
            <CardBody>
              <Row>
                <Col md={6}>
                  <FormGroup>
                    <Label>First Name</Label>
                    <InputGroup>
                      <InputGroupText>
                        <Icon icon="icon-park-outline:edit-name" />
                      </InputGroupText>
                      <Input
                        type="text"
                        value={firstName}
                        onChange={(ev) => setFirstName(ev.target.value)}
                      />
                    </InputGroup>
                  </FormGroup>
                </Col>
                <Col md={6}>
                  <FormGroup>
                    <Label>Last Name</Label>
                    <InputGroup>
                      <InputGroupText>
                        <Icon icon="icon-park-outline:edit-name" />
                      </InputGroupText>
                      <Input
                        type="text"
                        value={lastName}
                        onChange={(ev) => setLastName(ev.target.value)}
                      />
                    </InputGroup>
                  </FormGroup>
                </Col>
                <Col md={6}>
                  <FormGroup>
                    <Label>Email</Label>
                    <InputGroup>
                      <InputGroupText>
                        <Icon icon="ic:outline-email" />
                      </InputGroupText>
                      <Input
                        type="email"
                        value={email}
                        onChange={(ev) => setEmail(ev.target.value)}
                      />
                    </InputGroup>
                  </FormGroup>
                </Col>
                <Col md={6}>
                  <FormGroup>
                    <Label>Phone</Label>
                    <PhoneInput
                      country="us"
                      specialLabel=""
                      value={phone}
                      onChange={(phone) => setPhone(phone)}
                      inputStyle={{ width: "100%" }}
                    />
                  </FormGroup>
                </Col>
                <Col md={12}>
                  <div>
                    <Label>Address</Label>
                    <InputGroup>
                      <InputGroupText>
                        <Icon icon="ph:address-book" />
                      </InputGroupText>
                      <Input
                        type="text"
                        value={address}
                        onChange={(ev) => setAddress(ev.target.value)}
                        autoComplete="new-password"
                      />
                    </InputGroup>
                  </div>
                </Col>
              </Row>
            </CardBody>
            <CardFooter className="d-flex justify-content-end bg-white py-3">
              <Button
                disabled={profileLoading}
                color="primary"
                className="d-flex align-items-center gap-2"
                onClick={handleUpdateProfile}
              >
                {profileLoading ? (
                  <Spinner size="sm" />
                ) : (
                  <Icon icon="material-symbols:save-outline" fontSize={20} />
                )}
                Update Profile
              </Button>
            </CardFooter>
          </Card>
        </Col>
        <Col lg={6} xs={12}>
          <Card className="mb-3">
            <CardHeader className="bg-white p-3">
              <h4 className="mb-0">Change password</h4>
            </CardHeader>
            <CardBody>
              <FormGroup className="required">
                <Label>Current Password</Label>
                <InputGroup>
                  <InputGroupText>
                    <Icon icon="teenyicons:password-outline" />
                  </InputGroupText>
                  <Input
                    type="password"
                    value={prevPwd}
                    onChange={(ev) => setPrevPwd(ev.target.value)}
                    autoComplete="new-password"
                  />
                </InputGroup>
              </FormGroup>
              <FormGroup className="required">
                <Label>New Password</Label>
                <InputGroup>
                  <InputGroupText>
                    <Icon icon="solar:key-broken" />
                  </InputGroupText>
                  <Input
                    type="password"
                    value={password}
                    onChange={(ev) => setPassword(ev.target.value)}
                    autoComplete="new-password"
                  />
                </InputGroup>
              </FormGroup>
              <div className="required">
                <Label>Password Confirmation</Label>
                <InputGroup>
                  <InputGroupText>
                    <Icon icon="mdi:lock-check-outline" />
                  </InputGroupText>
                  <Input
                    type="password"
                    value={confirmPwd}
                    onChange={(ev) => setConfirmPwd(ev.target.value)}
                  />
                </InputGroup>
              </div>
            </CardBody>
            <CardFooter className="d-flex justify-content-end bg-white py-3">
              <Button
                disabled={passwordLoading}
                color="primary"
                className="d-flex align-items-center gap-2"
                onClick={handleUpdatePassword}
              >
                {passwordLoading ? (
                  <Spinner size="sm" />
                ) : (
                  <Icon icon="material-symbols:save-outline" fontSize={20} />
                )}
                Update Password
              </Button>
            </CardFooter>
          </Card>
        </Col>
      </Row>
    </Fragment>
  );
};

export default Profile;
